module.exports = "/**\r\n * Открытие сессии Криптосервиса\r\n */\r\n\r\n// параметры для создания новой сессии\r\nvar type = document.getElementById('open_type_input').value;\r\nvar profile = document.getElementById('open_profile_input').value;\r\n\r\n// ID сессии для открытия существущей сессии Криптосервиса, \r\n// если есть то другие параметры игнорируются\r\nvar sessionId = document.getElementById('open_session-id_input').value;\r\n\r\nvar openParams = {\r\n    type: type,\r\n    profile: profile,\r\n    id: sessionId\r\n}\r\n\r\n// открытие сессии\r\ncc.open(openParams).then(function(result) {\r\n    console.log('success\\nresult:', result);\r\n}, function(error) {\r\n    console.log('error', error);\r\n});\r\n"